import { Component } from '@angular/core';
import { MultiLangService } from '../shared/multi.service';
import { NgForm } from '@angular/forms';

@Component({
    selector:'corporate',
    templateUrl:'./corporate.component.html'
})

export class CorporateAccountComponent {

    lang:string;
    content:any;

    constructor(private multi:MultiLangService){ }

    ngOnInit(){
        this.lang = this.multi.lang
        this.getContent(this.lang)
        this.multi.tlang.subscribe(
            (lang:string) => {
                this.lang = lang;
                this.getContent(lang)
            }
        )
    }

    getContent(lang:string){
        if(lang === 'eng'){
            this.content = this.multi.corporatePageContent.eng;
        }
        else if(lang === 'som'){
            this.content = this.multi.corporatePageContent.som;
        }
        else if(lang === 'arb'){
            this.content = this.multi.corporatePageContent.arb;
        }
    }

    onSubmit(f:NgForm){
        this.multi.onSubmit(f, this.lang)
    }
}