import { NgModule } from "@angular/core";
import { RouterModule, Routes } from '@angular/router';

import { CorporateAccountComponent } from './corporate.component';

const appCRoutes: Routes = [
    { path:'', component: CorporateAccountComponent }
]

@NgModule({
    imports:[RouterModule.forChild(appCRoutes)],
    exports:[RouterModule]
})

export class CorporateRoutingModule { }